/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable react/no-unknown-property, jsx-a11y/label-has-for*/
import React from 'react'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import DSTruncatedTooltipText from '@elliemae/ds-basic/TruncatedTooltipText'
import RequiredMark from '@elliemae/ds-basic/form/RequiredMark'

const blockName = 'em-ds-label'

const Text = aggregatedClasses('span')(blockName, 'text')

const DSLabel = ({
  children, label, htmlFor, required, optional, truncateText = true,
}) => {
  return (
    <label className='em-ds-label' htmlFor={htmlFor}>
      {truncateText ? (
        <DSTruncatedTooltipText value={children || label} />
      ) : (
        <Text>
          {label}
        </Text>
      )}
      <RequiredMark optional={optional} required={required} />
    </label>
  )
}

export default DSLabel
