import React from 'react'
import PropTypes from 'prop-types'
import AddonWrapper from './AddonWrapper'

const DSInputGroup = ({
  containerProps = {},
  innerRef = null,
  className = '',
  style = {},
  leftAddon = null,
  rightAddon = null,
  children: InputComponents, // as InputComponents,
}) => {
  return (
    <AddonWrapper
      ref={innerRef}
      className={className}
      containerProps={containerProps}
      leftAddon={leftAddon}
      rightAddon={rightAddon}
      style={style}
    >
      {InputComponents}
    </AddonWrapper>
  )
}

DSInputGroup.propTypes = {
  containerProps: PropTypes.shape({}),
  innerRef: PropTypes.func,
  className: PropTypes.string,
  /**
   * Set style for the input group
   */
  style: PropTypes.objectOf(PropTypes.string),
  /**
   * Component to be added at the left side of the input
   */
  leftAddon: PropTypes.element,
  /**
   * Component to be added at the right side of the input
   */
  rightAddon: PropTypes.element,
  /**
   * Input components to show
   */
  children: PropTypes.element,
}

export default DSInputGroup
