/* eslint-disable max-lines */
import React, { Fragment } from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import getComponentFromProps from '@elliemae/ds-utilities/getComponentFromProps'
import DSTextBox from '@elliemae/ds-basic/form/TextBox'
import DSInputGroup from '@elliemae/ds-basic/form/InputGroup'
import DSFloatingLabelInput from '@elliemae/ds-basic/form/FloatingLabelInput'
import DSLabel from './Label/DSLabel'
import Feedback from './Feedback'
import DSError from './Error/DSError'
import { Suffix, SuffixInputWrapper, SuffixWrapper } from './Suffix/Suffix'
import { maskTypes } from '../InputMask/MaskTypes'

export { DSError }

const blockName = 'form-item'

const Wrapper = aggregatedClasses('div')(blockName, null, ({ leftLabel, variant }) => ({
  'with-left-label': leftLabel,
  [variant]: variant,
}))
const WrapperContent = aggregatedClasses('div')(`${blockName}-content`)

export const FORMITEM_LAYOUT_VARIANTS = {
  DEFAULT: 'variant-default',
  FOCUS: 'variant-focus',
  ACTIVE: 'variant-active',
  DISABLED: 'variant-disabled',
  READ_ONLY: 'variant-read-only',
  OPTIONAL: 'variant-optional',
}


const DSFormItemLayout = ({
  leftLabel = false,
  floatingLabel = false,
  name,
  id,
  value,
  inputComponent: InputComponent = DSTextBox,
  labelComponent: LabelComponent = DSLabel,
  feedbackComponent: FeedbackComponent = Feedback,
  labelText,
  truncateText,
  hasError = false,
  // orientation = 'vertical', // one of [ vertical, horizontal ]
  validationState, // one of [ success, error, warning ]
  validationMessage = '',
  feedbackMessage = '',
  readOnly,
  focused = false,
  type,
  autoFocus = () => {},
  disabled = false,
  onChange = () => {},
  onBlur = () => {},
  onFocus = () => {},
  onPaste = () => {},
  onKeyUp = () => {},
  extraInputProps = {},
  extraContent,
  leftAddon = null,
  rightAddon = null,
  required = false,
  optional = false,
  mask,
  useSubfix = '',
  suffix = '',
  variant = FORMITEM_LAYOUT_VARIANTS.DEFAULT,
  containerProps = {},
  ...rest
}) => {
  const floatingLabelOverride = floatingLabel === 'UNSAFE'

  // group the input props to not duplicate it inside the specific inputComponent (floating or not)
  const inputProps = {
    ...extraInputProps,
    disabled,
    hasError: hasError || validationState === 'error',
    id,
    name,
    type,
    autofocus: autoFocus,
    onBlur,
    onChange,
    onFocus,
    onPaste,
    onKeyUp,
    readOnly,
    value,
    mask,
    useSubfix,
    variant,
    required,
    ...rest,
  }

  const renderInput = Input => (leftAddon || rightAddon
    ? (
      <DSInputGroup
        leftAddon={leftAddon}
        rightAddon={rightAddon}
        variant={variant}
      >
        {Input}
      </DSInputGroup>
    ) : Input)

  const renderFloating = (
    <DSFloatingLabelInput
      {...inputProps}
      extraInputProps={extraInputProps}
      focused={focused}
      inputComponent={InputComponent}
      labelComponent={LabelComponent}
      labelText={labelText}
      optional={optional}
      required={required}
      variant={variant}
    />
  )

  const InputWithLabel = floatingLabelOverride
    ? renderInput(renderFloating)
    : (
      <Fragment>
        <LabelComponent
          htmlFor={id || name}
          label={labelText}
          optional={optional}
          required={required}
          truncateText={truncateText}
        />
        {renderInput(getComponentFromProps(InputComponent, inputProps))}
      </Fragment>
    )

  if (suffix) {
    return (
      <Wrapper
        classProps={{
          disabled,
          readOnly,
          hasError,
          leftLabel,
          variant,
        }}
        {...containerProps}
      >
        <SuffixWrapper className='suffix-wrapper'>
          <SuffixInputWrapper className='suffix-input-wrapper'>
            <WrapperContent>
              {InputWithLabel}
              {extraContent}
            </WrapperContent>
            <FeedbackComponent
              hasError={hasError}
              text={validationState || hasError ? validationMessage : feedbackMessage}
            />
          </SuffixInputWrapper>
          <Suffix
            className='suffix-label'
            label={!!labelText}
            withFeedback={Boolean(validationMessage || feedbackMessage)}
          >
            {suffix}
          </Suffix>
        </SuffixWrapper>
      </Wrapper>
    )
  }

  return (
    <Wrapper
      classProps={{
        disabled,
        readOnly,
        hasError,
        leftLabel,
        variant,
      }}
      {...containerProps}
    >
      <WrapperContent>
        {InputWithLabel}
        {extraContent}
      </WrapperContent>
      <FeedbackComponent
        hasError={hasError}
        text={validationState || hasError ? validationMessage : feedbackMessage}
      />
    </Wrapper>
  )
}

DSFormItemLayout.propTypes = {
  containerProps: PropTypes.shape({}),
  leftLabel: PropTypes.bool,
  floatingLabel: PropTypes.bool,
  name: PropTypes.string,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
  ]),
  /** Any input component */
  inputComponent: PropTypes.element,
  /** Label component  */
  labelComponent: PropTypes.element,
  feedbackComponent: PropTypes.node,
  labelText: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element,
  ]),
  hasError: PropTypes.bool,
  validationState: PropTypes.oneOf(['success', 'error', 'warning']), // one of [ success, error, warning ]
  validationMessage: PropTypes.string,
  feedbackMessage: PropTypes.string,
  readOnly: PropTypes.bool,
  focused: PropTypes.bool,
  type: PropTypes.string,
  autoFocus: PropTypes.func,
  disabled: PropTypes.bool,
  onChange: PropTypes.func,
  onBlur: PropTypes.func,
  extraInputProps: PropTypes.element,
  extraContent: PropTypes.element,
  leftAddon: PropTypes.element,
  rightAddon: PropTypes.element,
  required: PropTypes.bool,
  optional: PropTypes.bool,
  mask: PropTypes.oneOfType([
    PropTypes.oneOf(maskTypes),
    PropTypes.string,
  ]),
  useSubfix: PropTypes.string,
  suffix: PropTypes.string,
  variant: PropTypes.oneOf([
    FORMITEM_LAYOUT_VARIANTS.DEFAULT,
    FORMITEM_LAYOUT_VARIANTS.FOCUS,
    FORMITEM_LAYOUT_VARIANTS.ACTIVE,
    FORMITEM_LAYOUT_VARIANTS.DISABLED,
    FORMITEM_LAYOUT_VARIANTS.READ_ONLY,
    FORMITEM_LAYOUT_VARIANTS.OPTIONAL,
  ]),
}

export default DSFormItemLayout
