import React from 'react'
import { convertPropToCssClassName } from '@elliemae/ds-classnames'

/**
 * if we don't care about word break
 * const regx = new RegExp(`.{1,${maxCharacters}}`, 'g')
 * text.match(regx)
 * @param {*} param0
 */
const DSTextWrapper = ({
  containerProps = {},
  className = '',
  style = {},
  text = '',
  maxCharacters = 0,
  maxWidth = '100%',
  textEllipsis = false,
  textAlignment = 'left',
  wordBreak = true,
}) => {
  const { cssClassName, classNameElement } = convertPropToCssClassName(
    'text-wrapperRef',
    className, {
      textEllipsis,
      textAlignment,
      wordBreak,
    })
  const limitPerCharacter = () => {
    return text.split(' ').reduce(
      (result, word) => {
        let resultIndex = result.length - 1
        if (result[resultIndex].length + word.length > maxCharacters) resultIndex += 1
        result[resultIndex] = `${result[resultIndex] || ''} ${word}`
        return result
      },
      [''],
    )
  }

  let displayText = text

  if (textEllipsis) displayText = maxCharacters > 0 ? displayText.substring(0, maxCharacters) : displayText
  else if (maxCharacters > 0) {
    displayText = limitPerCharacter().map((textpart, index) => (
      <span key={index} className={classNameElement('line')}>
        {textpart}
      </span>
    ))
  }

  return (
    <span
      {...containerProps}
      className={cssClassName}
      style={{
        maxWidth: maxCharacters < 1 ? maxWidth : null,
        ...style,
      }}
    >
      {textEllipsis ? (
        <span className={classNameElement('ellipsis-wrapperRef')}>
          {displayText}
        </span>
      ) : displayText}
    </span>
  )
}

export default DSTextWrapper
